.container-register {
  background: #f7f3eb;
  box-shadow: 2px 10px 20px 0px black;
  height: 50%;
  width: 25%;
  display: flex;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 3rem;
  padding-bottom: 3rem;
  padding-right: 1rem;
  padding-left: 1rem; }

form {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
  height: 100%;
  align-items: center; }

h2 {
  margin-bottom: 1rem; }

label {
  font-size: 1vw;
  align-self: flex-start;
  color: #a09e99; }

.button-register {
  display: block;
  border-radius: 20px;
  width: 10%;
  flex-grow: 1;
  margin-top: 2%;
  margin: 8px 0px; }

.move-button {
  margin-top: 3rem;
  height: 100%;
  width: 100%;
  border-radius: 10px; }

@media (max-width: 580px) {
  .container-register {
    width: 70%; } }
