/*
  FONTS
*/
@font-face {
  font-family: 'TitlePluton';
  src: url("./fonts/HVD Fonts - PlutoSansMedium.otf"); }

.notification-container {
  background: rgba(96, 181, 214, 0.5);
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1.3rem;
  padding-right: 1.3rem;
  border-radius: 20px; }

/*
    Section of mixings
  */
/*Mixing*/
* {
  margin: 0;
  padding: 0; }

html, body {
  height: 100vh;
  width: 100vw; }

.App {
  height: 100vh;
  width: 100vw; }

/*Mixin for side container*/
.section-1 {
  box-sizing: border-box;
  flex-basis: 95%; }

.header, .section-1 {
  display: grid;
  grid-template-columns: 1fr 5fr 1fr; }

.menu-box {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem; }

.menu-left {
  font-family: "PlutonGeneral", Fallback, sans-serif;
  background: var(--primary-dark);
  font-size: 0.7rem;
  padding-left: 1rem;
  /* adding margin to side containers*/
  padding-right: 1rem;
  /* adding margin to side containers*/
  padding-top: 1.5rem; }
  .menu-left p {
    font-weight: bold;
    color: var(--primary-white);
    font-size: 0.7rem;
    padding-bottom: 0.4rem; }
  .menu-left ul {
    color: var(--primary-blue);
    list-style-type: none;
    padding: 0%;
    text-decoration: none; }
    .menu-left ul li {
      padding-top: 0.6rem;
      padding-bottom: 0.6rem; }
    .menu-left ul a {
      margin-left: 0.5rem; }
    .menu-left ul a:hover {
      text-decoration: none;
      color: white; }

.menu-right {
  background: var(--primary-dark);
  color: var(--primary-grey);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 0.7rem;
  padding-left: 1rem;
  /* adding margin to side containers*/
  padding-right: 1rem;
  /* adding margin to side containers*/
  padding-top: 1.5rem; }
  .menu-right p {
    font-weight: bold;
    color: var(--primary-grey);
    font-size: 0.7rem;
    padding-bottom: 0.4rem; }
  .menu-right ol {
    overflow: hidden; }
  .menu-right li {
    font-size: 0.75rem;
    padding-top: 0.7rem;
    padding-bottom: 0.7rem; }

.menu-left a, .menu-right a {
  text-decoration: none;
  color: inherit; }

.element {
  color: var(--primary-blue); }

.logo {
  height: 100%; }

.content {
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  background: var(--third-white);
  flex-flow: column;
  padding: 1rem 4rem 0rem 4rem; }
  .content .listing-manipulator {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    margin-bottom: 1rem; }
    .content .listing-manipulator .content-filter-box a {
      text-decoration: none;
      color: var(--primary-grey); }
    .content .listing-manipulator h2 {
      font-size: 1.1rem;
      font-family: 'SecondTitlePluton', Fallback, sans-serif;
      font-weight: bold;
      color: var(--secondary-dark); }
    .content .listing-manipulator p {
      text-decoration: none;
      color: var(--primary-grey); }
    .content .listing-manipulator .layout-form-box {
      color: var(--primary-blue); }
  .content .books {
    display: flex;
    justify-items: stretch;
    align-items: space-between;
    flex-wrap: wrap; }
    .content .books .book {
      display: flex;
      flex-basis: 18%;
      margin-left: auto;
      flex-flow: column;
      overflow: hidden; }
      .content .books .book img {
        width: 15rem;
        height: 20rem; }
      .content .books .book .book-title {
        font-family: "TitlePluton", Fallback, sans-serif;
        overflow: hidden;
        white-space: nowrap;
        overflow: hidden;
        font-weight: bold;
        font-size: 0.9rem;
        color: var(--secondary-dark); }

.selected {
  color: var(--primary-white); }

@media (max-width: 750px) {
  .content {
    justify-content: flex-start; }
  .books {
    flex-wrap: wrap;
    flex-direction: row; } }
