.full-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url("../../images/LoginImage.jpg");
  background-repeat: no-repeat;
  background-size: cover; }

.container-login {
  background: #f7f3eb;
  box-shadow: 2px 10px 20px 0px black;
  height: 40%;
  width: 25%;
  display: flex;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0.7rem; }

.login-title {
  justify-self: center;
  align-self: center;
  flex-grow: 5;
  justify-content: center; }

.input-container {
  width: 90%; }

.button-submit {
  display: block;
  border-radius: 20px;
  width: 90%;
  flex-grow: 1;
  margin-top: 2%;
  margin: 8px 0; }

.messages {
  font-size: 0.7rem; }

.messages-error {
  color: red; }

.messages-success {
  color: green; }

@media (max-width: 580px) {
  .full-container {
    padding-top: 1rem;
    justify-content: flex-start; }
  .container-login {
    width: 70%; } }

.move-button {
  margin-top: 3rem;
  height: 100%;
  width: 100%;
  border-radius: 10px; }
