/*Mixin for side container*/
.menu-left {
  font-family: "PlutonGeneral", Fallback, sans-serif;
  background: #041d29;
  padding-left: 1rem;
  /* adding margin to side containers*/
  padding-right: 1rem;
  /* adding margin to side containers*/
  padding-top: 1.5rem; }
  .menu-left p {
    font-weight: bold;
    color: #fafcfe;
    font-size: 0.8rem;
    padding-bottom: 0.4rem; }
  .menu-left ul {
    color: #60b5d6;
    list-style-type: none;
    padding: 0%;
    font-size: 0.8rem; }
    .menu-left ul li {
      padding-top: 0.6rem;
      padding-bottom: 0.6rem; }
    .menu-left ul button {
      margin-left: 0.5rem;
      background: none;
      border: none; }
    .menu-left ul button:hover {
      color: white; }
    .menu-left ul .element {
      color: #60b5d6; }
    .menu-left ul .selected {
      color: white;
      text-decoration-color: white; }
